<template>
  <div class="cont">
    <div class="search-area">
      <!-- <el-col :span="8"> -->
      <el-input v-model="search_word" placeholder="请输入你的问题"
        ><el-button icon="el-icon-search" slot="append" @click="go"></el-button
      ></el-input>
      <!-- </el-col> -->
    </div>
    <div class="answer">
      <p style="font-size: large; font-family: '幼圆'">{{ answer }}</p>
    </div>
    <div class="background-bottom"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      search_word: "",
      answer: "",
    };
  },
  methods: {
    go() {
      this.answer = `${this.search_word}是什么/谁呢，顾名思义，${this.search_word}就是${this.search_word}，${this.search_word}大家应该都懂吧，${this.search_word}太${this.search_word}了，其实就是太${this.search_word}了了。那么${this.search_word}为什么会太${this.search_word}了，相信大家都很好奇是怎么回事。大家可能会感到很惊讶，${this.search_word}怎么会太${this.search_word}了呢？但事实就是这样，我也感到非常惊讶。那么这就是关于${this.search_word}太${this.search_word}了的事情了，大家有没有觉得很神奇呢？总的来说，懂得都懂。`;
    },
  },
  computed: {
    search() {
      return this.search_word;
    },
  },
  watch: {
    search(value) {
      if (value == "") {
        this.answer = "";
      }
    },
  },
};
</script>
